<style lang="scss" scoped>
.guide-main{
    width: 100%;
    height: 100%;
    .first_column{
        width: 100%;
        padding: 80px 100px;
        box-sizing: border-box;
        .title{
            font-size: 35px;
            font-weight: bold;
            margin-bottom: 25px;
        }
        .sub_title{
            font-size: 16px;
            color: #878787;
        }
        .box_list{
            padding: 25px 0;
            box-sizing: border-box;
            height: 290px;
            display: flex;
            justify-content: space-around;
            .box_item{
                text-decoration: none;
                background: white;
                box-sizing: border-box;
                padding:15px 0;
                width: 250px;
                height: 250px;
                border-radius: 5px;
                transition: all 0.6s;
                color: #878787;
                font-weight: bold;
                cursor: pointer;
                align-items: center;
                align-content: center;
                .box_icon{
                    margin-top:50px;
                    font-size: 60px;
                    color: #3399ea;
                }
            }
            .box_item:hover{
                transform:translate(0,-10px);
                border-bottom: 4px solid #d6d6d6;
                background: #3d3d3d;
                .box_icon{
                    color: #d6d6d6;
                }
            }
        }
    }
    .separate{
        display: flex;
        justify-content: space-between;
        width: 100%;
        .separate_item{
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 260px 660px 0 0;
            border-right-width: 100%;
            border-color: #3399ea transparent transparent transparent;
        }
        .right_separate{
            transform: rotate(180deg); 
        }
        
    }
    .dynamic-img-wrap{
        align-items: center;
        position: relative;
        // height: 600px;
        
        width:100%;
        // padding-left: 3%;
        .dynamic-img1{
            left: calc(50% - 445px);
            position: absolute;
            top: 0;
        };
        .dynamic-img2-wrap{
            position: absolute;
            left: calc(50% - 445px);
            // height: 350px;
            overflow: hidden;
            max-height: 529px;
            top: 0;
        }
    }
    .guide_footer{
        width: 100%;
        height: 60px;
        box-sizing: border-box;
        margin-top: 50px;
        padding: 20px 0;
        font-size: 18px;
        color: #CFD9F7;
        font-weight: bold;
        background: #3399ea;
    }
    .count{
        width: 100%;
        // justify-content: flex-start;
        text-align: left;
        .title{
            width: 100%;
            box-sizing: border-box;
            padding: 15px 80px;
            border-bottom: 1px solid #CBCCCD;
        }
        .container{
            padding: 15px 0 0 80px;
        }
    }
    .pictrue{
        cursor: pointer;
        filter:grayscale(100%);
        margin: 5px;
    }
    .pictrue:hover{
        filter:grayscale(0);
    }
}
</style>
<template>
    <div class="guide-main">
        <div class="item_column first_column">
            <div class="title">这是一个人事管理系统...</div>
            <div class="sub_title">此系统实现公司内部不同岗位的员工对个人信息和公司公共信息的操作</div>
            <div class="item_row box_list">
                <router-link to="/login" class="item_column box_item">
                    <i class="el-icon-user-solid box_icon"></i>
                    <div class="box_text">前往登录</div>
                </router-link>
                <router-link to="/" class="item_column box_item">
                    <i class="el-icon-s-promotion box_icon"></i>
                    <div class="box_text">前往使用</div>
                </router-link>
                <router-link to="/register" class="item_column box_item">
                    <i class="el-icon-circle-plus box_icon"></i>
                    <div class="box_text">前往注册</div>
                </router-link>
            </div>
        </div>
        <!-- <img src="@/assets/image/index/bg-bw.jpg" width="100%"/> -->
        <div class="separate">
            <div class="separate_item left_separate "></div>
            <div class="separate_item right_separate"></div>
        </div>
        <div class="item_column first_column ">
            <div class="title">平台赞助商&技术支持</div>
            <div class="sub_title">服务器、技术、其它形式</div>
            <div class="item_row box_list">
                <img src="@/assets/image/index/huawei.png" class="pictrue" height="80px"/>
                <img src="@/assets/image/index/jianshu.png" class="pictrue" height="80px"/>
                <img src="@/assets/image/index/element.png" class="pictrue" height="80px"/>
                <img src="@/assets/logo.png" class="pictrue" height="80px"/>
                <img src="@/assets/image/index/yidong.png" class="pictrue" height="80px">
            </div>
        </div>
        <div class="item_column dynamic-img-wrap" id="dynamic-img-wrap">
            <img src="@/assets/image/index/index-blue.png" width="890px" height="529px" class="dynamic-img1" id="dynamic-img1"/>
            <div class="dynamic-img2-wrap" id="dynamic-img2-wrap" style="height: 1px;">
                <img src="@/assets/image/index/index-red.png" width="890px" height="529px"/>
            </div>
        </div>
        <div style="width:100%;padding: 290px 0;">
        </div>
        <div class="count">
            <div class="title">流量统计</div>
            <div class="container">总访问量 16,036，本月访问量 524，昨日访问量 87，今日访问量 7，当前在线 1</div>
        </div>
        <div class="item_column guide_footer">
            <div>网站归于站长，最终解释权归站长所有</div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return{
            
        }
    },
    created(){
        
    }
}
</script>



